.close-button {
  position: absolute;
  z-index: 10;
  color: #2c2930;
  cursor: pointer;
}
[data-whatinput=mouse] .close-button {
  outline: 0;
}
.close-button:hover, .close-button:focus {
  color: #cb3c29;
}
.close-button.small, .close-button.meeting-polls__button {
  right: 0.66rem;
  top: 0.33em;
  font-size: 1.5em;
  line-height: 1;
}

.close-button.medium, .close-button {
  right: 1rem;
  top: 0.5rem;
  font-size: 2em;
  line-height: 1;
}

.label {
  display: inline-block;
  padding: 0.33333rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  line-height: 1;
  white-space: nowrap;
  cursor: default;
  background: #cb3c29;
  color: #fff;
}
.label.primary {
  background: #cb3c29;
  color: #fff;
}
.label.secondary {
  background: #39747f;
  color: #fff;
}
.label.success {
  background: #57d685;
  color: #1a181d;
}
.label.warning {
  background: #ffae00;
  color: #1a181d;
}
.label.alert {
  background: #ec5840;
  color: #1a181d;
}

.progress {
  height: 1rem;
  margin-bottom: 1rem;
  border-radius: 4px;
  background-color: #e8e8e8;
}
.progress.primary .progress-meter {
  background-color: #cb3c29;
}
.progress.secondary .progress-meter {
  background-color: #39747f;
}
.progress.success .progress-meter {
  background-color: #57d685;
}
.progress.warning .progress-meter {
  background-color: #ffae00;
}
.progress.alert .progress-meter {
  background-color: #ec5840;
}

.progress-meter {
  position: relative;
  display: block;
  width: 0%;
  height: 100%;
  background-color: #cb3c29;
  border-radius: 4px;
}

.progress-meter-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
