  position: relative;
}
.chart-tooltip .tooltip-content::after, .chart-tooltip .tooltip-content::before {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.chart-tooltip .tooltip-content::after {
  border-color: rgba(255, 255, 255, 0);
